<template>
<div :class="$style.wrap">
  <div :class="$style.header">
    <div :class="$style.logo">
      <a href="/">
        <img src="../../assets/m-play/logo.png" alt="">
      </a>
    </div>
    <div :class="$style.btnBox">
      <a href="javascript:void(0)" :class='[$style.btn, $style.open]' @click='gotoDownload'>APP?????????</a>
      <a href="javascript:void(0)" :class='[$style.btn, $style.download]' @click='gotoDownload'>??????APP</a>
    </div>
  </div>
  <div :class="$style.playWrap" class="playWrap" v-show='!((liveStatus == 1 && warmUpVideoList.length === 0) || (liveStatus == 2 && (playbackUrl == "" || result.playbackStatus == 0)))'>
    <video :class="['video-js', 'vjs-melephant']" playsinline="playsinline"></video>
    <div :class="[$style.statusLogo, $style.statusIng]" v-if='liveStatus === 0'>
      <i></i>
      ?????????
    </div>
    <div :class="[$style.statusLogo, $style.statusDone]" v-if='liveStatus === 2 && !(playbackUrl == "" || result.playbackStatus == 0)'>
      <i></i>
      ??????
    </div>
    <div :class="$style.people">
      {{ text }}
    </div>
  </div>
  <div :class='[$style.poster]' v-show='(liveStatus == 1 && warmUpVideoList.length === 0) || (liveStatus == 2 && (playbackUrl == "" || result.playbackStatus == 0))'>
    <div :class="$style.done" v-show='liveStatus == 2 && (playbackUrl == "" || result.playbackStatus == 0)'>
      ?????????????????????
    </div>
    <img :src="`//image.youxiang0210.com/${result.displayImageUrl}`" :class='(liveStatus == 2 && (playbackUrl == "" || result.playbackStatus == 0)) ? $style.cover: ""' alt="">
    <div :class="$style.people">
      {{text}}
    </div>
  </div>
  <div :class='[$style.status, status >= 4 ? $style.statusReady : ""]'>
    <template v-if='status < 4'>
      <h1>{{result.title}}</h1>
      <div :class="$style.subTitle">
        {{ status == 1 ? "???????????????" : "??????????????????" }}
      </div>
      <div :class="$style.clock">
        <div :class="[$style.days, $style.block]">{{time.days}}???</div>
        <div :class="[$style.hours, $style.block]">{{time.hours}}</div>
        <div :class="[$style.mins, $style.block]">{{time.mins}}</div>
        <div :class="[$style.secs, $style.block]">{{time.secs}}</div>
      </div>
    </template>
    <template v-else>
      <h1>{{result.title}}</h1>
      <span :class='$style.sub'>???????????????{{fmtTime(timeLeft)}}</span>
    </template>
  </div>
  <div :class="$style.activity">
    <div :class="$style.details">
      <h2>????????????</h2>
      <p>{{result.detail}}</p>
    </div>
    <div :class="$style.details">
      <h2>????????????</h2>
      <p>{{result.location}}</p>
    </div>
    <div :class="$style.details">
      <h2>????????????</h2>
      <p>{{result.organizer}}</p>
    </div>
    <div :class="$style.details">
      <h2>????????????</h2>
      <p>{{result.partner}}</p>
    </div>
  </div>
</div>
</template>

<script>
const videojs = window.videojs
import commonMixins from '@/mixins/common'

export default {
  name: 'e-live',

  mixins: [commonMixins],

  data() {
    return {
      liveRoomId: '',
      vOptions: {
        autoplay: true,
        sources: [],
        controls: true,
        width: document.body.offsetWidth,
        height: '423',
        poster: '',
      },
      liveStatus: -1, // 1??????????????????0???????????????2????????????
      warmUpVideoList: [],
      currentWarmUpVideoIndex: 0,
      playbackUrl: '',
      timeLeft: Date.now(),
      result: {
        displayImageUrl: '',
        playbackStatus: 0
      },
      time: {
        days: '00',
        hours: '00',
        mins: '00',
        secs: '00'
      },
      record: {},
    }
  },

  computed: {
    status() {
      let status

      if (this.liveStatus === 2) {
        status = 5 // ????????????
      } else if (this.liveStatus === 0) {
        status = 4 // 1.????????????????????????2.????????????????????????????????????
      } else if (this.liveStatus === 1 && this.warmUpVideoList.length !== 0 && this.timeLeft - Date.now() > 0) {
        status = 3 // ?????????????????????????????????
      } else if (this.liveStatus === 1 && this.timeLeft - Date.now() <= 0) {
        status = 1 // ??????????????????????????????
      } else if (this.liveStatus === 1 && this.warmUpVideoList.length === 0) {
        status = 2 // ?????????????????????????????????
      }
      return status
    },
    text() {
      if (this.liveStatus === 1) {
        console.log(2222)
        return `${this.record.interested}?????????`
      } else if (this.liveStatus === 0) {
        return `${this.record.watching}???????????????`
      } else if (this.liveStatus === 2) {
        console.log(11111)
        return `${this.record.watched}?????????`
      }
    }
  },

  created() {
    // alert(window.devicePixelRatio)
    setTimeout(() => {
      // alert(document.documentElement.getBoundingClientRect().width)
      // alert(document.querySelector('meta[name="viewport"]').getAttribute('content'))
      // alert(document.documentElement.style.fontSize)
      // alert(document.documentElement.clientWidth)
    }, 3000)
    const liveRoomId = this.getUrlParam('roomId')
    this.liveRoomId = liveRoomId
    Promise.all([
      this.$fetch('/m_web/liveRoom/view', { liveRoomId }),
      this.$fetch('/m_web/liveRoom/warmUpVideoList', { liveRoomId }),
      this.$fetch('/m_web/liveRoom/getRecordByLiveRoomId', { liveRoomId })
    ]).then((result) => {
      console.log(result)
      if (result[0].code !== 0) return
      this.handleRoomDetail(result[0].data)
      this.handleWarmUpVideos(result[1].data)
      this.handleRecord(result[2].data)
      if (!((this.liveStatus === 1 && this.warmUpVideoList.length === 0) || (this.liveStatus === 2 && this.playbackUrl === ''))) {
        this.init() // 1.????????????????????????2.????????????????????????????????????
      }
      this.fetchWechatShare()
    })
  },

  methods: {
    fetchWechatShare() {
      var oReq = new XMLHttpRequest()

      oReq.onload = (e) => {
        var data = oReq.response
        console.log(data, typeof data)
        if (data) {
          /* global wx:true */
          data = JSON.parse(data)
          wx.config({
            // debug: true,
            appId: 'wxc89ca66545c2df54',
            timestamp: data.timestamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
          })

          wx.ready(() => {
            wx.onMenuShareAppMessage({
              title: this.result.title,
              desc: this.result.detail.substring(0, 30),
              imgUrl: 'https://image.youxiang0210.com/wechat-share.png'
            })
            wx.onMenuShareTimeline({
              title: this.result.title,
              imgUrl: 'https://image.youxiang0210.com/wechat-share.png'
            })
          })
        }
      }
      oReq.open('POST', '/wechat/get_params')
      oReq.send()
    },
    handleRecord(data) {
      this.record = data
    },
    handleRoomDetail(data) {
      console.log(data)
      const result = data
      this.result = result
      document.title = this.result.title
      this.liveStatus = result.liveStatus
      console.log(this.liveStatus)
      this.timeLeft = result.scheduleTime
      this.playbackUrl = result.playbackUrl
      if (this.liveStatus === 0) { // ????????????????????????????????????
        this.vOptions.sources.push({
          src: result.hlsPlayUrl,
          type: 'application/x-mpegURL'
        })
      }
      if (this.liveStatus === 2 && this.playbackUrl !== null) { // ?????????????????????
        this.vOptions.sources.push({
          src: this.playbackUrl,
          type: 'application/x-mpegURL'
        })
      }
      this.vOptions.poster = `//image.youxiang0210.com/${result.displayImageUrl}`
      if (Date.now() < this.timeLeft && this.liveStatus === 1) { // ?????????????????????????????????
        this.refreshTime()
      }
    },
    fmtTime(timeStamp) {
      const date = new Date(timeStamp)

      const year = date.getFullYear()
      const month = date.getMonth() + 1
      const day = date.getDate()
      const hour = date.getHours()
      const mins = date.getMinutes()
      const secs = date.getSeconds()

      return `${year}-${this.fixNum(month)}-${this.fixNum(day)}  ${this.fixNum(hour)}:${this.fixNum(mins)}:${this.fixNum(secs)}`
    },
    handleWarmUpVideos(data) {
      const result = data
      for (let i = 0; i < result.length; i++) {
        const item = result[i].vVideoDetailRspVos
        for (let j = 0; j < item.length; j++) {
          this.warmUpVideoList.push(`https://video.youxiang0210.com/${item[j].videoResourceStatusVo.m3u8Clarity.url}`)
        }
      }

      if (this.warmUpVideoList.length !== 0 && this.liveStatus === 1) {
        this.vOptions.sources.push({
          src: this.warmUpVideoList[0],
          type: 'application/x-mpegURL'
        })
        this.currentWarmUpVideoIndex = 0
      }
    },
    gotoDownload() {
      // let ifr = document.createElement('iframe')
      // ifr.src = 'com.daxiang.live://go?action=videoPlay&videoId=10000128&videoDetailId=800000123'
      // ifr.style.display = 'none'
      // document.body.appendChild(ifr)
      // location.href = 'https://fir.im/liveIOS'

      // setTimeout(() => {
      //   document.body.removeChild(ifr)
      // }, 2000)

      location.href = 'jcgroup://youxiang.com/go?action=livePlay&id=' + this.liveRoomId
      const now = Date.now()
      setTimeout(() => {
        if (Date.now() - now < 2550) {
          location.href = 'http://www.youxiang0210.com/m-index.html'
        }
      }, 2500)
    },
    refreshTime() {
      this.tid = setInterval(() => {
        this.time = this.countdown()
      }, 1000)
    },
    fixNum(num) {
      if (num > 9) {
        return num
      } else {
        return `0${num}`
      }
    },
    countdown() {
      const now = Date.now()
      if (now > this.timeLeft) {
        clearInterval(this.tid)
        return {
          days: '00',
          hours: '00',
          mins: '00',
          secs: '00'
        }
      }
      const timeLag = Math.floor((this.timeLeft - now) / 1000)

      const time = {
        days: this.fixNum(Math.floor(timeLag / 86400)),
        hours: this.fixNum(Math.floor((timeLag / 3600) % 24)),
        mins: this.fixNum(Math.floor((timeLag / 60) % 60)),
        secs: this.fixNum(Math.floor((timeLag % 60)))
      }

      return time
    },
    init() {
      let controlBar = {
        children: [
          'playToggle',
          'currentTimeDisplay',
          'progressControl',
          'durationDisplay',
          'fullscreenToggle',
        ]
      }
      var that = this

      const options = this.vOptions
      const theComponent = this
      let videoOpiton = {
        controls: options.controls, // ????????????
        autoplay: false, // ????????????
        width: options.width,
        height: options.height,
        poster: options.poster || '', // ??????
        techOrder: ['html5', 'flash'],
        sources: options.sources || [],
        controlBar,
        errorDisplay: false, // ????????????????????????
        loadingSpinner: false, // ????????????????????????
      }

      this.player = null
      this.player = videojs(document.querySelector('.video-js'), videoOpiton, function() {
        theComponent.buildLoadingSpinner()
        theComponent.buildErrorModal(this)
        theComponent.buildModalComp()

        this.addChild('Loading')
        this.addChild('ErrorDisplay')
        this.addChild('EndDisplay')

        this.on('error', () => {
          this.childNameIndex_.ErrorDisplay.show()
          this.childNameIndex_.Loading.hide()
          this.childNameIndex_.EndDisplay.hide()
        })

        this.on('seeking', () => {
          this.childNameIndex_.Loading.show()
        })
        this.on('waiting', () => {
          this.childNameIndex_.Loading.show()
        })

        this.on('play', () => {
          this.childNameIndex_.ErrorDisplay.hide()
          this.childNameIndex_.Loading.hide()
          this.childNameIndex_.EndDisplay.hide()
        })

        this.on('ended', () => {
          if (that.currentWarmUpVideoIndex + 1 < that.warmUpVideoList.length) {
            that.currentWarmUpVideoIndex = that.currentWarmUpVideoIndex + 1
            this.src({
              src: that.warmUpVideoList[that.currentWarmUpVideoIndex],
              type: 'application/x-mpegURL'
            })
          } else {
            that.currentWarmUpVideoIndex = 0
            this.src({
              src: that.warmUpVideoList[that.currentWarmUpVideoIndex],
              type: 'application/x-mpegURL'
            })
          }
        })

        this.on('fullscreenchange', (screen) => {
          if (!this.isFullscreen()) {
            this.childNameIndex_.ErrorDisplay.hide()
            this.childNameIndex_.Loading.hide()
            this.childNameIndex_.EndDisplay.hide()
            this.controls(true)
          }
        })

        // loading
        const loadingHideList = ['playing', 'canplay']
        for (let i = 0, j = loadingHideList.length; i < j; i++) {
          this.on(loadingHideList[i], () => {
            this.childNameIndex_.Loading.hide()
          })
        }
      })
      window.addEventListener('resize', () => {
        this.player.width(document.body.offsetWidth)
      })
    },
    buildLoadingSpinner() {
      let loadingComponent = videojs.getComponent('LoadingSpinner')
      let Loading = videojs.extend(loadingComponent, {
        constructor: function(player, options) {
          loadingComponent.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <img class="loading-gif" src="${require('../../assets/m-play/loading.gif')}" alt="">
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-loading vjs-hidden'
          })
        },
      })
      videojs.registerComponent('Loading', Loading)
    },
    buildErrorModal(player) {
      // let that = this
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'ErrorDisplay'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <p class="error-text">??????????????????</p>
                <p class="error-reload"><i class="iconfont icon-refresh"></i>????????????</p>
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-error vjs-hidden'
          })
        },
        handleClick() {
          // console.log(that)
          // that.dispose()
          // that.init()
          // player.play()
          location.reload()
        }
      })
      videojs.registerComponent('ErrorDisplay', theComponent)
    },
    dispose() {
      this.player.dispose()
      const videoEl = document.createElement('video')
      videoEl.setAttribute('class', 'video-js vjs-melephant')
      videoEl.setAttribute('playsinline', 'playsinline')
      document.querySelector('.playWrap').appendChild(videoEl)
    },
    buildModalComp() {
      let that = this
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'EndDisplay'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <i class="iconfont icon-download2"></i>
                <span class="end-text">??????????????????app</span>
              </div>
            `,
            className: 'vjs-m-fullscreen vjs-melephant-ended vjs-hidden'
          })
        },
        handleClick() {
          that.gotoDownload()
        }
      })
      videojs.registerComponent('EndDisplay', theComponent)
    },
  }
}
</script>

<style lang="postcss" module>
:global(.vjs-live .vjs-progress-control) {
  display: block !important;
  visibility: hidden;
}
:global(.vjs-live .vjs-duration) {
  visibility: hidden;
  width: 4rem !important;
}
:global(.vjs-live .vjs-current-time-display) {
  visibility: hidden;
}
:global(.vjs .vjs-duration-display) {
  display: none;
}
:global(.video-js) {
  height: 5.64rem;
}
.wrap {
  .header{
    width: 100%;
    background: #0A0A0A;
    height: 1.3333333333rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .logo {
    margin-left: 0.32rem;
    height: 0.8rem;
    a {
      display: block;
    }
    img {
      height: 0.8rem;
    }
  }
  .btnBox {
    height: 0.8rem;
    display: flex;
    margin-right: 0.266666rem;
  }
  .btn {
    box-sizing: border-box;
    display: block;
    width: 2.4rem;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    border-radius: 0.1rem;
  }
  .open {
    color: #FABE00;
    border: 1px solid rgba(250, 190, 0, 0.5);
  }
  .download {
    margin-left: 0.266666rem;
    background: #FABE00;
    color: #fff;
    font-weight: 500;
  }
  .playWrap {
    overflow: hidden;
    background: #000;
    height: 5.64rem;
    user-select: none;
    box-shadow: 0 -200px 150px 100px #000;
  }
  .statusLogo {
    padding: 5px 10px;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.2);
    position: absolute;
    color: #FFFFFF;
    font-size: 11px;
    top: 1.5rem;
    left: 0.5rem;
    i {
      height: 8px;
      width: 12px;
      display: inline-block;
    }
  }
  .statusIng {
    i {
      background-image: url('../../assets/m-live/ing.png');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  .statusDone {
    i {
      background-image: url('../../assets/m-live/done.png');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  .people {
    position: absolute;
    right: 0.1rem;
    top: 1.5rem;
    padding: 5px 8px;
    color: #fff;
    font-size: 11px;
  }
  .done {
    position: absolute;
    top: 3.5rem;
    left: 50%;
    color: #FABE00;
    width: 100px;
    margin-left: -50px;
    text-align: center;
    z-index: 9;
  }
  .cover {
    opacity: 0.5;
  }
  .poster {
    background: #000;
    height: 5.64rem;
    user-select: none;
    box-shadow: 0 -200px 150px 100px #000;
    img {
      height: 100%;
      width: 100%;
    }
  }
  .status {
    text-align: center;
    padding-bottom: 0.8rem;

    &.statusReady {
      padding: 0.3rem 0.3rem;
      text-align: left;

      h1 {
        justify-content: flex-start;
        text-align: left;
        margin: 0;
        font-size: 20px;
        margin-bottom: 8px;
      }
      .sub {
        font-size: 14px;
        color: #D2B685;
        letter-spacing: 1px;
      }
    }
    h1 {
      display: flex;
      justify-content: center;
      text-align: center;
      font-size: 22px;
      color: #222222;
      letter-spacing: 1px;
      margin: 20px auto 0;
    }
  }
  .subTitle {
    justify-content: center;
    display: flex;
    text-align: center;
    width: 6rem;
    font-weight: 500;
    margin: 0.3rem auto 0;
    font-size: 15px;
    color: #BF9751;
    letter-spacing: 1px;
    opacity: 0.7;
    background-image: url('../../assets/m-live/line.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  .clock {
    background-image: url('../../assets/m-live/clock.png');
    /*background-color: #F5F5F5;
    border-radius: 1.4933333rem;*/
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 1.5rem;
    width: 6.8rem;
    margin: 24px auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .block {
    background: #D5A85A;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    color: #FFFFFF;
    height: 1.06666rem;
    line-height: 1.06666rem;
    text-align: center;
    width: 0.96rem;
    margin: 0 0.226666666rem 0 0.22666666rem;
  }
  .days {
    width: 1.41333rem;
  }
  .activity {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 20px 10px;
    border-top: 1px solid #eeeeee;
    width: 94.666666%;
  }
  .details {
    margin-bottom: 0.333333rem;
    h2 {
      font-size: 15px;
      color: #4A4A4A;
      letter-spacing: 0.54px;
    }
    p {
      opacity: 0.8;
      margin-top: 0.2rem;
      font-size: 14px;
      color: #555555;
      letter-spacing: 1px;
      line-height: 0.6rem;
    }
  }
}
</style>



